import React, { Component } from 'react'

export default class Skills extends Component{
  render() {
    return (

      <section className="colorlib-skills" data-section="skills">
        <div className="colorlib-narrow-content">
          <div className="row">
            <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
              <span className="heading-meta">My Specialty</span>
              <h2 className="colorlib-heading animate-box">My Skills</h2>
            </div>
          </div>
          <div className="row">
            <div className="col-md-12 animate-box" data-animate-effect="fadeInLeft">
              <p>My skill and specialization</p>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInLeft">
              <div className="progress-wrap">
                <h3>Python 3+</h3>
                <div className="progress">
                  <div className="progress-bar color-1" role="progressbar" aria-valuenow={75} aria-valuemin={0} aria-valuemax={100} style={{width: '75%'}}>
                    <span>75%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
              <div className="progress-wrap">
                <h3>Flask</h3>
                <div className="progress">
                  <div className="progress-bar color-2" role="progressbar" aria-valuenow={70} aria-valuemin={0} aria-valuemax={100} style={{width: '60%'}}>
                    <span>70%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInLeft">
              <div className="progress-wrap">
                <h3>HTML5</h3>
                <div className="progress">
                  <div className="progress-bar color-3" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{width: '85%'}}>
                    <span>60%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
              <div className="progress-wrap">
                <h3>CSS3</h3>
                <div className="progress">
                  <div className="progress-bar color-4" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{width: '90%'}}>
                    <span>60%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInLeft">
              <div className="progress-wrap">
                <h3>JS</h3>
                <div className="progress">
                  <div className="progress-bar color-5" role="progressbar" aria-valuenow={40} aria-valuemin={0} aria-valuemax={100} style={{width: '70%'}}>
                    <span>40%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
              <div className="progress-wrap">
                <h3>React</h3>
                <div className="progress">
                  <div className="progress-bar color-7" role="progressbar" aria-valuenow={20} aria-valuemin={0} aria-valuemax={100} style={{width: '80%'}}>
                    <span>20%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
              <div className="progress-wrap">
                <h3>Git</h3>
                <div className="progress">
                  <div className="progress-bar color-8" role="progressbar" aria-valuenow={50} aria-valuemin={0} aria-valuemax={100} style={{width: '80%'}}>
                    <span>50%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
              <div className="progress-wrap">
                <h3>MongoDB</h3>
                <div className="progress">
                  <div className="progress-bar color-9" role="progressbar" aria-valuenow={75} aria-valuemin={0} aria-valuemax={100} style={{width: '80%'}}>
                    <span>75%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
              <div className="progress-wrap">
                <h3>SQL</h3>
                <div className="progress">
                  <div className="progress-bar color-10" role="progressbar" aria-valuenow={50} aria-valuemin={0} aria-valuemax={100} style={{width: '80%'}}>
                    <span>50%</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
              <div className="progress-wrap">
                <h3>Rest API</h3>
                <div className="progress">
                  <div className="progress-bar color-11" role="progressbar" aria-valuenow={75} aria-valuemin={0} aria-valuemax={100} style={{width: '80%'}}>
                    <span>75%</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
};